            class="headerStyle"
          >
            <h3 style="margin:auto;" class="fontFamily">
              <span style="float:left;" class="fontFamily headresize">United Token Pre-Sale progress</span>
            </h3>
          </md-card-header>
          <md-card-content
            class="paddingStyleD1"
            style="text-align: center;padding-bottom: 13px;padding-top: 7px;"
          >
          <br>
            <vue-countdown-timer
              @start_callback="startCallBack('event started')"
              @end_callback="endCallBack('event ended')"
              :start-time="'2020-09-22 00:00:00'"
              :end-time="'2020-10-30 00:00:00'"
              :interval="1000"
              :end-text="'Event ended!'"
              :day-txt="':'"
              :hour-txt="':'"
              :minutes-txt="':'"
              :seconds-txt="' '"
            >
              <template slot="start-label" slot-scope="scope">
                <span
                  style="color: red"
                  v-if="scope.props.startLabel !== '' && scope.props.tips && scope.props.labelPosition === 'begin'"
                >{{scope.props.startLabel}}:</span>
                <span
                  style="color: blue"
                  v-if="scope.props.endLabel !== '' && !scope.props.tips && scope.props.labelPosition === 'begin'"
                >{{scope.props.endLabel}}:</span>
              </template>

              <template slot="countdown" slot-scope="scope">
                <table style="width:80%;margin-left:auto;margin-right:auto;">
                  <tr class="fontFamily" style="font-size:25px;">
                    <td>{{scope.props.days}}</td>
                    <td>{{scope.props.dayTxt}}</td>
                    <td>{{scope.props.hours}}</td>
                    <td>{{scope.props.hourTxt}}</td>
                    <td>{{scope.props.minutes}}</td>
                    <td>{{scope.props.minutesTxt}}</td>
                    <td>{{scope.props.seconds}}</td>
                    <td>{{scope.props.secondsTxt}}</td>
                  </tr>
                  <tr>
                    <td class="fontFamily" style="font-size:12px;">DAYS</td>
                    <td>&nbsp;</td>
                    <td class="fontFamily" style="font-size:12px;">HOURS</td>
                    <td>&nbsp;</td>
                    <td class="fontFamily" style="font-size:12px;">MINUTES</td>
                    <td>&nbsp;</td>
                    <td class="fontFamily" style="font-size:12px;">SECONDS</td>
                    <td>&nbsp;</td>
                  </tr>
                </table>
              </template>

              <template slot="end-label" slot-scope="scope">
                <span
                  style="color: red"
                  v-if="scope.props.startLabel !== '' && scope.props.tips && scope.props.labelPosition === 'end'"
