                <span
                  style="color: blue"
                  v-if="scope.props.endLabel !== '' && !scope.props.tips && scope.props.labelPosition === 'end'"
                >{{scope.props.endLabel}}:</span>
              </template>

              <template slot="end-text" slot-scope="scope">
                <span style="color: green">{{ scope.props.endText}}</span>
              </template>
