<script src="dist/clipboard.min.js"></script>
<script>
import axios from "axios";
import Stepper from "@/components/wizard1";
import { APIService } from "../../../apiservice.js";
import { Modal } from "@/components";
import QrcodeVue from 'qrcode.vue'
var qr = require('qr-image');

export default {
  components: { Stepper,Modal,QrcodeVue},
  data() {
    return {
       copytxt2:true,
      copytxt3:false,
      ethadd:'',
       qrCode:'',
        qrAddress:'',
        qrAmount:'',
      bitcoininfo: false,
      ethereuminfo: false,
      bankinfo: false,
      paypalinfo: false,
      emptyScreen: true,

      alertbutton: false,
      alertbutton1: false,
      alertbutton2: false,
      invest: {
        ethaddress: "",
        amount: "",
        coin: "",
        ethereum:"",
        bitcoin:"",
        euro:"",
      },

      cryptosBTC: [],
      cryptosETH: [],
      btccalc: [],
      btccalc1: [],
      ethcalc: [],
      ethcalc1: [],
      eurcalc: [],
      eurcalc1: [],
      errors: [],
      value: "",
      value2: "0.01",
      stepperOptions: {
        headers: [
          { title: "", required: true },
          { title: "", required: true },
          { title: "", required: true },
          { title: "" },
        ],
        prevText: "Previous",
        nextText: "Continue",
       
      },
    };
  },
  methods: {
     copytxt1(){
 this.copytxt2 = false;
 this.copytxt3 = true;
    },
    // noticeModalHide: function () {
    //   this.alertbutton = false;
    // },
    // noticeModalHide1: function () {
    //   this.alertbutton1 = false;
    // },
    //  noticeModalHide2: function () {
    //   this.alertbutton2 = false;
    // },
    dashboard() {
      // window.location.href="/dashboard";
      // this.$router.push({path:"/dashboard"})
      location.reload();
    },
    investdata() {
      let self = this;
      let ethaddress = self.invest.ethaddress;
      self.invest.amount = this.value;
      let amount = self.invest.amount;
      self.invest.ethereum=this.ethcalc1;
      let ethereum = self.invest.ethereum;
      self.invest.bitcoin=this.btccalc1;
      let bitcoin = self.invest.bitcoin;
      self.invest.euro=this.eurcalc1;
      let euro = self.invest.euro;
      let coin = self.invest.coin;
      let data = {
        ethaddress,
        amount,
        coin,
        ethereum,
        bitcoin,
        euro
      };
      APIService.createToken(data).then((res) => {
        if (res.status == 200) {
          this.$root.$emit("pass");
          console.log("qrcode", res);
          this.qrAddress = res.data.address;
          this.qrAmount = res.data.amount;
          this.qrCode = qr.imageSync(res.data.address, { type: 'svg' });
        
        }
      });
    },
  },
  mounted() {
   this.$root.$on("continue", (value) => {
      console.log("returndata recieved", value);
      if (value == 0) {
        if (this.invest.ethaddress) {
          this.$root.$emit("pass");
        } else {
          this.alertbutton = true;
        }
      } else if (value == 1) {
        if (this.value ){
            if(!this.invest.coin){
              this.alertbutton2 = true;  
             
            }else{
               this.$root.$emit("pass");
              
            }
        }else{
              this.alertbutton1 = true;  
        }

      }else if(value == 2)(
         this.investdata()
      )
    });
  
  },
  watch: {
    value() {
      this.btccalc = this.value / this.cryptosBTC.EUR;
      this.btccalc1 = parseFloat(this.btccalc / 100).toFixed(5);

      this.ethcalc = this.value / this.cryptosETH.EUR;
      this.ethcalc1 = parseFloat(this.ethcalc / 100).toFixed(5);

      this.eurcalc = this.value / 100;
      this.eurcalc1 = parseFloat(this.eurcalc).toFixed(5);

      this.invest.amount = this.value;
      console.log("close", this.value);
      this.$root.$emit("checkdata", this.invest);
    },
    "invest.ethaddress"() {
      if (this.invest.ethaddress) {
        let self = this;

        self.invest.amount = this.value;
        console.log("close", this.value);
        this.$root.$emit("checkdata", this.invest);
      }
    },

    "invest.coin"() {
      // if(this.invest.coin != 'Ethereum' && this.invest.coin != 'Bitcoin' && this.invest.coin != 'BankTransfer' && this.invest.coin != 'Paypal'){
      // this.emptyScreen=true
      // }
      if (this.invest.coin == "Ethereum") {
        this.ethereuminfo = true;
        (this.bitcoininfo = false),
          // this.ethereuminfo=false,
          (this.bankinfo = false),
          (this.paypalinfo = false),
          (this.emptyScreen = false);
      } else if (this.invest.coin == "Bitcoin") {
        this.bitcoininfo = true;
        // this.bitcoininfo=false,
        (this.ethereuminfo = false),
          (this.bankinfo = false),
          (this.paypalinfo = false),
          (this.emptyScreen = false);
      } else if (this.invest.coin == "Bank") {
        this.bankinfo = true;

        (this.bitcoininfo = false),
          (this.ethereuminfo = false),
          // this.bankinfo=false,
          (this.paypalinfo = false),
          (this.emptyScreen = false);
      } else if (this.invest.coin == "Paypal") {
        this.paypalinfo = true;
        (this.bitcoininfo = false),
          (this.ethereuminfo = false),
          (this.bankinfo = false),
          // this.paypalinfo=false
          (this.emptyScreen = false);
      }
    },
  },
  created() {
    axios
      .get(
        "https://min-api.cryptocompare.com/data/pricemulti?fsyms=BTC,ETH&tsyms=USD,EUR"
      )
      .then((response) => {
        this.cryptosBTC = response.data.BTC;
        this.cryptosETH = response.data.ETH;
      })
      .catch((e) => {
        this.errors.push(e);
      });
  },
};
</script>

